<template>
    <div>
        <div class="formbold-input-flex">    
            <div>
                <label for="currentTransactionDate" class="formbold-form-label">  </label>
                <ValidationProvider name="  "  v-slot="{ errors }">
                <input type="date" class="formbold-form-input" id="currentTransactionDate" v-model="currentTransactionDate">
                <span class="error">{{ errors[0] }}</span>
                </ValidationProvider>
            </div>
            <div>
                <label for="currentAmount" class="formbold-form-label"></label>
                <ValidationProvider name=""  v-slot="{ errors }">
                <input type="number" class="formbold-form-input" placeholder=" " id="currentAmount" v-model="currentAmount">
                <span class="error">{{ errors[0] }}</span>
                </ValidationProvider>
            </div>
        </div>
        <div class="formbold-mb-3 spichial-cont-btns">
          <button class="addProduct-btn spichialAdd-btn" @click="clearTransaction"> </button>
          <button class="addProduct-btn spichialAdd-btn" :class="{ 'disabled-btn': currentTransactionDate=='' || currentAmount=='' }" :disabled="currentTransactionDate=='' || currentAmount==''" @click="addPayment"> </button>
        </div>
    </div>
  </template>
  
  <script>

  import { mapActions } from 'pinia';

  // store
  import { useTransactionsStore } from '@/store/transactions/transactions.js';

  // to make id for payment : use in (1) for-loop to show them (2) select to update
  import { v4 as uuidv4 } from 'uuid';

  import { extend } from 'vee-validate';
  import { required , numeric , min_value  } from 'vee-validate/dist/rules';

  // Register rules with custom messages

  extend('required', {
  ...required,
  message: '{_field_} '
  
  });

  extend('numeric', {
  ...numeric,
  message: '{_field_}    '
  });

  extend('min_value', {
  ...min_value,
  message: '{_field_}      {min}'
  });

  extend('date_format', {
  validate(value) {
    const regex = new RegExp('^\\d{4}-\\d{2}-\\d{2}$')
    return regex.test(value);
  },
  params: ['format'],
  message: '{_field_}      {format}'
  });

  export default {
    name: "ActionPayments",
    props: ["transactionInfo","isMaterialActiveOrTechnical"],

    components: {
       
   },
    computed: {
      getDarkMode() {
        return this.$store.state.darkMode;
      },
    },
    created(){
  
    },
    
    data() {
    return {
      currentTransactionDate:"",
      currentAmount:"",
    };
   },

   methods: {
     
     ...mapActions(useTransactionsStore, ['updateTransaction','fetchTransactionsByUserId']),

    async addPayment() {

      if (!this.currentTransactionDate || !this.currentAmount) {
        alert("   ");
        return;
      }

      const newPayment = {
        id: uuidv4(),
        transactionDate: this.currentTransactionDate,
        amount: this.currentAmount.toString(),
      };

      if (this.transactionInfo.transactionType === "") {
        newPayment.type = this.isMaterialActiveOrTechnical ? "material" : "industrial";
      }

      //     `transactionInfo`   
      const updatedTransaction = { ...this.transactionInfo };
      updatedTransaction.payments = [...(updatedTransaction.payments || []), newPayment];

      await this.updateTransaction(updatedTransaction);
      await this.fetchTransactionsByUserId(updatedTransaction.userId);

      this.currentTransactionDate = "";
      this.currentAmount = "";
    },

    async clearTransaction() {
      let totalAmount;
      let paymentType;

      if (this.transactionInfo.transactionType === "") {
        //  
        totalAmount = parseFloat(this.transactionInfo.amount) || 0;
        paymentType = null; //     
      } else if (this.transactionInfo.transactionType === "") {
        //  
        if (this.isMaterialActiveOrTechnical) {
          totalAmount = parseFloat(this.transactionInfo.amount) || 0; //  
          paymentType = "material";
        } else {
          totalAmount = parseFloat(this.transactionInfo.manufacturingAmount) || 0; //  
          paymentType = "industrial";
        }
      }

      //      `paymentType`    (   "")
      const paidAmount = this.transactionInfo.payments
        .filter(payment => !paymentType || payment.type === paymentType)
        .reduce((sum, payment) => sum + parseFloat(payment.amount), 0);

      const remainingAmount = totalAmount - paidAmount;

      if (remainingAmount > 0) {
        const newPayment = {
          id: uuidv4(),
          transactionDate: new Date().toISOString().split("T")[0], //  
          amount: remainingAmount.toString(),
          type: paymentType || null,
        };

        const updatedTransaction = { ...this.transactionInfo };
        updatedTransaction.payments = [...(updatedTransaction.payments || []), newPayment];

        await this.updateTransaction(updatedTransaction);
        await this.fetchTransactionsByUserId(updatedTransaction.userId);

        alert("        .");
      } else {
        alert("     .");
      }
  }

  
  
  },

  };
  //  console.log(pro)
  </script>
  <style scoped lang="scss">
  
  .spichial-cont-btns{
    display: flex;
    justify-content: space-evenly;
  }
  .spichialAdd-btn{
    width: 100px;

  }
  </style>
  